import { _decorator, Component, Node, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('BZDSceneMgt')
export class BZDSceneMgt extends Component {

    @property(Label)
    public roomId: Label
    @property(Label)
    public gamecount: Label

    @property(Label)
    seat0Name: Label
    @property(Label)
    seat0TotalPoints: Label
    @property(Label)
    seat0JuPoints: Label
    @property(Label)
    seat0PanPoints: Label

    @property(Label)
    seat1Name: Label
    @property(Label)
    seat1TotalPoints: Label
    @property(Label)
    seat1JuPoints: Label
    @property(Label)
    seat1PanPoints: Label

    @property(Label)
    seat2Name: Label
    @property(Label)
    seat2TotalPoints: Label
    @property(Label)
    seat2JuPoints: Label
    @property(Label)
    seat2PanPoints: Label

    @property(Label)
    seat3Name: Label
    @property(Label)
    seat3TotalPoints: Label
    @property(Label)
    seat3JuPoints: Label
    @property(Label)
    seat3PanPoints: Label
    
    start() {
        this.init();
    }

    private init(){
        globalThis._eventtarget.on("requestRoomInfo", this.onRequestRoomInfo, this);
    }

    update(deltaTime: number) {
        
    }

    public onRequestRoomInfo(data){
        console.log("onrequestroomInfo：",data);
        let roomInfo = data.roomInfo;
        this.roomId.string = roomInfo.roomId;
        this.gamecount.string = roomInfo.currentNumbers + "/" + roomInfo.gameNumbers + "局 " + roomInfo.scoreNumbers + "分";
    }
}

